@import url(https://fonts.googleapis.com/css2?family=Anton&family=Cookie&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  color: white;
  width: 100%;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #2f3535;
  background-attachment: fixed;
  background-size: cover;
  background-position: right;
  background-image: url(/background.jpg);
}

body::-webkit-scrollbar{
  background-color: black;
}

body::-webkit-scrollbar-thumb{
  background-color: orange;
  border-radius: 10px;
}

body::-webkit-scrollbar-thumb:hover{
  background-color: rgb(255, 193, 78);
  border-radius: 10px;
}

button{
  transition: 0.5s;
}
.block-pop-up{
  position: fixed;
  top:0;
  width: 100%;
  left: 0;
  height: 100%;
  background-color: rgba(36, 35, 53, 0.788);
  z-index: 19;
  color:white;
}

.hidden-scroll{
  overflow: hidden;
}

.loading-block{
  position: absolute;
  top:50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.button-scroll-top{
  position: fixed;
  bottom: 0;
  right: 0;
  z-index: 10;
  padding: 10px;
  color: white;
  background-color: black;
  margin:1rem;
  cursor: pointer;
  transition: 0.5s ease-in-out;
  border-radius:10px;
}
.nav-bar__app{
  display: flex;
  list-style-type: none;
  background-color: rgb(2, 2, 2);
  margin: 0;
  padding: 6px 0;
  z-index:11;
  width: 100%;
  position: fixed;
  top:0;
  color: white;
  transition: 0.5s ease-in-out;
  box-shadow: 0 0 2px 1px rgb(100, 100, 100);
}

.nav-bar + div{
  margin-top: 72px;
}

.nav-bar__item .active{
  transition: 0.3s ease-in-out;
  background-color: white;
  color: black!important;
}

.input-choose-avatar{
  height: 0;
  width: 0;
  background:none ;
  border:none;
  outline: none;
}

.left-nav-item{
  margin: 0 0 0 auto!important
}

.left-nav-item div{
  padding:10px;
}


.child-nav-bar__app{
  display: flex;
  list-style-type: none;
  background-color: transparent;
  margin: 0;
  padding: 0;
  padding-top: 10px;
  z-index:11;
  width: 100%;
  position: static;
  top:0;
  color: white;
}

.toggle-show__nav{
  display: none; 
  padding-left: 10px;
  color:white;
  z-index: 100;
  padding-top: 0.5rem;
  padding-right: 1rem;
}

.logo a{
  display: flex;
  align-items: center;
  text-decoration: none;
  border-radius: 10px;
}

.logo-wrapper{
  display: flex;
  align-items: center;
}

.logo-wrapper > span{
  font-weight: 800;
  color: white;
  font-size: 2.5rem;
  font-family: 'Cookie', cursive;
}

.nav-bar__app .nav-bar__item{
  padding: 10px;
  min-width: -webkit-fit-content;
  min-width: -moz-fit-content;
  min-width: fit-content;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  font-size: 1.3rem;
}

.nav-bar__app .nav-bar__item a,.nav-bar__app .nav-bar__item span{
  text-decoration: none;
  color: white;
  padding: 10px;
  border-radius: 10px;
}

.avatar-user{
  width: 34px;
  height: 34px;
  border-radius: 50%;
}

@media (max-width:1186px){
  .nav-bar__app{
    flex-direction: column;
    padding: 11px 0;
  }
  .nav-bar + div{
    margin-top: 76px;
  }  
  .child-nav-bar__app{
    display: none;
  }
  .child-nav-bar__app{
    flex-direction: column;
  }
  .left-nav-item{
    margin: 0 0 auto 0!important;
  }
  
  .toggle-show__nav{
    display: block; 
  }
}
